import React from 'react'
import { useParams } from 'react-router-dom'
import { Evaluator as EvaluatorComponent } from '@aserto/console-common'

import Frame from '../../Frame'

const Evaluator: React.FC = () => {
  const { id: instanceName } = useParams()

  return (
    <Frame>
      <EvaluatorComponent authorizerPolicyInstance={{ authorizerInstance: instanceName }} />
    </Frame>
  )
}

export default Evaluator
